<script>
// Vue helpers
import vSelect from 'vue-select'
// Database
import firebase from '../db'
// Component
import Plates from '@/components/Plates'
// Admin
import env from "../user";
export default {
  name: "Dashboard",
  data() {
    return {
      user: '',
      showMenu: false,
      errorInput: false,
      showData: false,
      isLoading: true,
      showDash: true,
      errorMsg: 'Error Something Wrong',
      day: 0,
      admin: false,
      stations: {},
      mainData: {
        dej: {
          s1: {
            plates: [
              {name: '', imgUrl: ''}
            ]
          }
          
        },
        din: {
          s1: {
            plates: [
              {name: '', imgUrl: ''}
            ]
          }
        },
      },
      days: [
        {id: 0, name: 'Dimanche'},
        {id: 1, name: 'Lundi'},
        {id: 2, name: 'Mardi'},
        {id: 3, name: 'Mercredi'},
        {id: 4, name: 'Jeudi'},
        {id: 5, name: 'Vendredi'},
        {id: 6, name: 'Samedi'},
      ],
      newpass: {
        pass: '',
        valid: '',
      },
      showPassInVa: false,
      logerror: false,
      copy: '',
      regemail: '',
      regpassword: '',
      regrepassword: '',
      regshowusererror: false,
      regshowatuherror: false,
      regrepasserror: false,
      regerror: false,
      inptype: 'password',
      changePwd: false
    }
  },
  components: {
    vSelect,
    Plates
  },
  methods: {
    logout() {
      this.$store.dispatch('logout').then(() => this.$router.push('/'))
    },
    getToday() {
      let date = new Date()
      let day = date.getDay()
      return day      
    },
    addPlate(data) {
      if (data[data.length - 1].name)
        data.push({name: '', imgUrl: ''})
      else {
        this.errorInput = true
        this.errorMsg = 'Nom du plate est vide'
      }
    },
    checkData() {
      this.isLoading = true
      firebase.database().ref('chef/' + this.day).on('value', snapshot => {
        let plates = snapshot.val()
        if(plates) {
          this.mainData = plates
          this.showData = true
          this.isLoading = false
        }
        else
          this.mainData = null
          this.isLoading = false
      });
    },
    deletePlate(data, i) {
      let arrLen = data.length
        while(arrLen--)
          if(arrLen == i)
              data.splice(i, 1)
    },
    getData() {
      this.isLoading = true
      firebase.database().ref('chef/' + this.day).on('value', snapshot => {
        let plates = snapshot.val()
        if(plates) {
          this.copy = plates
          this.showData = false
          this.isLoading = false
        }
        else
          this.copy = null
          this.isLoading = false
      });
    },
    getStations() {
      firebase.database().ref('stations').on('value', snapshot => {
        let station = snapshot.val()
        if(station) {
          for (let index = 0; index < station.length; index++) {
            const element = station[index]
            if (element) {
              this.$set(this.stations, index, element)
              this.$set(this.mainData.dej, `s${index}`, {plates: [{name: ''}]})
              this.$set(this.mainData.din, `s${index}`, {plates: [{name: ''}]})
            }
            if(this.mainData)
              this.isLoading = false
          }
        }
      });
    },
    addStation() {
      let sl = Object.keys(this.stations).length
      this.$swal({
          title: 'tes-vous sr ?',
          text: "Lorsque vous ajoutez une station, vous rinitialisez toute la semaine",
          icon: 'info',
          showCancelButton: true,
          confirmButtonColor: '#0023b4',
          cancelButtonColor: '#17C671',
          confirmButtonText: 'Oui, ajouter',
          cancelButtonText: 'Annuler'
      }).then((result) => {
          if (result.value) {
            let newStation = this.stations[sl + 1] = {
              id: sl + 1,
              name: `s${sl + 1}`,
              people: 4
            }
            firebase.database().ref('stations/' + (sl + 1)).set(newStation)
            for (let i = 0; i < 7; i++)
              firebase.database().ref('chef/' + i).remove()
            this.$swal({
                icon: 'success',
                title: 'Votre station a t ajoute',
                showConfirmButton: false,
                timer: 1500
            })
          }
      })
      

    },
    deleteStation(id) {
      this.$swal({
          title: 'tes-vous sr ?',
          text: "Lorsque vous supprimez une station, vous rinitialisez toute la semaine",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#e53e3e',
          cancelButtonColor: '#17C671',
          confirmButtonText: 'Oui, supprimer',
          cancelButtonText: 'Annuler'
      }).then((result) => {
          if (result.value) {
            firebase.database().ref('stations/' + id).remove()
            firebase.database().ref('chef/' + this.day).remove()
            this.$router.go()
            this.$swal({
                icon: 'success',
                title: 'Votre station a t supprime',
                showConfirmButton: false,
                timer: 1500
            })
          }
      })
    },
    saveData() {
      let s1Dej = this.dataValidation(this.mainData.dej.s1.plates)
      let s1Din = this.dataValidation(this.mainData.din.s1.plates)
      let s2Dej = this.dataValidation(this.mainData.dej.s2.plates)
      let s2Din = this.dataValidation(this.mainData.din.s2.plates)
      let s3Dej = this.dataValidation(this.mainData.dej.s3.plates)
      let s3Din = this.dataValidation(this.mainData.din.s3.plates)
      let s1DejLength = this.mainData.dej.s1.plates.length
      let s1DinLength = this.mainData.din.s1.plates.length
      let s2DejLength = this.mainData.dej.s2.plates.length
      let s2DinLength = this.mainData.din.s2.plates.length
      let s3DejLength = this.mainData.dej.s3.plates.length
      let s3DinLength = this.mainData.din.s3.plates.length
      if (s1DejLength > s1Dej) {
        this.errorInput = true
        this.errorMsg = 'Nom du plate est vide Dans le Djeuner du Station 1'
      }
      else if (s1DinLength > s1Din) {
        this.errorInput = true
        this.errorMsg = 'Nom du plate est vide Dans le dner du Station 1'
      }
      else if (s2DejLength > s2Dej) {
        this.errorInput = true
        this.errorMsg = 'Nom du plate est vide Dans le Djeuner du Station 2'
      }
      else if (s2DinLength > s2Din) {
        this.errorInput = true
        this.errorMsg = 'Nom du plate est vide Dans le dner du Station 2'
      }
      else if (s3DejLength > s3Dej) {
        this.errorInput = true
        this.errorMsg = 'Nom du plate est vide Dans le Djeuner du Station 3'
      }
      else if (s3DinLength > s3Din) {
        this.errorInput = true
        this.errorMsg = 'Nom du plate est vide Dans le dner du Station 3'
      }
      else {
        firebase.database().ref('chef/' + this.day).set(this.mainData)
        .then(() => {
          this.$router.go()
        })
      }
    },
    dataValidation(object) {
      let i = 0
      for (let index = 0; index < object.length; index++) {
        const name = object[index].name
        if (name)
          i++
      }
      return i
    },
    resetData() {
      this.$swal({
          title: 'tes-vous sr ?',
          text: "Vous ne pourrez pas revenir sur cela!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#e53e3e',
          cancelButtonColor: '#17C671',
          confirmButtonText: 'Oui, supprimer',
          cancelButtonText: 'Annuler'
      }).then((result) => {
          if (result.value) {
            firebase.database().ref('chef/' + this.day).remove()
            this.$swal({
                icon: 'success',
                title: 'Votre journe a t supprime',
                showConfirmButton: false,
                timer: 1500
            })
          }
      })
    },
    resetAllData() {
      this.$swal({
          title: 'tes-vous sr ?',
          text: "Vous ne pourrez pas revenir sur cela!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#e53e3e',
          cancelButtonColor: '#17C671',
          confirmButtonText: 'Oui, supprimer',
          cancelButtonText: 'Annuler'
      }).then((result) => {
          if (result.value) {
            for (let i = 0; i < 7; i++)
              firebase.database().ref('chef/' + i).remove()
            this.$swal({
                icon: 'success',
                title: 'Votre semaine a t supprime',
                showConfirmButton: false,
                timer: 1500
            })
          }
      })
    },
    getImage(e, sta, time, num) {
      const file = e.target.files
      let filename = file[0].name
      if (filename.lastIndexOf('.') <= 0) {
        return this.$swal({
            icon: 'error',
            title: 'Please select a valide image',
            showConfirmButton: false,
            timer: 15000
        })
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.mainData[time][sta]['plates'][num].imgUrl = fileReader.result
      })
      fileReader.readAsDataURL(file[0])
    },
    saveForm() {
      if (this.regEmptyInput()) {
          this.$store.dispatch('register', {email: this.regemail.toLowerCase(), password: this.regpassword})
          .then(() => {
            this.$store.dispatch('logout').then(() => this.$router.push('/'))
            // this.$router.go()
          })
          .catch(err => {
              this.regmessage = err.message
              this.regerror = true
          })
      }
    },
    regEmptyInput() {
      if (!this.regemail) {
          this.regshowusererror = true
          this.regshowatuherror = false
          this.regrepasserror = false
          return false
      }
      else if (!this.regpassword) {
          this.regshowusererror = false
          this.regshowatuherror = true
          this.regrepasserror = false
          return false
      }
      else if (this.regpassword != this.regrepassword) {
          this.regshowusererror = false
          this.regshowatuherror = false
          this.regrepasserror = true
      }
      else
          return true
    },
    showpassword() {
        if (this.inptype == 'password')
            this.inptype = 'text'
        else
            this.inptype = 'password'
    },
    hideRegError() {
      this.regshowusererror = false
      this.regshowatuherror = false
      this.regrepasserror = false
      this.regerror = false
    },
    changeStatus() {
      this.showDash = !this.showDash
      this.showMenu = false
    },
    changePassword() {
      if(this.newpass.pass && this.newpass.valid && (this.newpass.pass == this.newpass.valid)) {
        let user = firebase.auth().currentUser
        user.updatePassword(this.newpass.pass)
        .then(() => {
          this.$store.dispatch('logout').then(() => this.$router.push('/'))
        })
        .catch(err => {
          this.logerror = true
          this.errorMsg = err.message
        })
      } else
        this.showPassInVa = true
    }
  },
  created() {
    this.user = firebase.auth().currentUser
    if (this.user.email == env.email)
      this.admin = true
    this.day = this.getToday()
    this.getData()
    this.getStations()
  }
}
</script>

